import api from "../../utis/api"
export default {
    namespaced: true,
    state: {
        isLoggedIn: false,
        user: {}
    },
    mutations: {
        LOGIN(state, data) {
            state.user = data;
            state.isLoggedIn = true;
            console.log(data)
            api.defaults.headers.common["Token"] = data.token;
        },
        LOGOUT(state) {
            state.user = [],
                state.isLoggedIn = false;
            api.defaults.headers.common["Token"] = ''
        }
    },
    actions: {
        login({ commit }, LoginForm) {
            return api
                .post("/login", LoginForm, {
                    _slient: true
                })
                .then(
                    res => {
                        commit("LOGIN", res)
                    },
                    err => {
                        console.log(err);
                    }
                );
        },
        logout({ commit }) {
            commit("LOGOUT")
        },
        register({ commit }, LoginForm) {
            return api
                .post("/register", LoginForm, {
                    _slient: true
                })
                .then(
                    res => {
                        commit("LOGIN", res)
                    },
                    err => {
                        console.log(err);
                    }
                );
        }
    }
}