<template>
  <div>
    <div class="topbar">
      <div class="container">
        <div class="topbar-nav">
          <a href>小米商城</a>
          <span>|</span>
          <a href>MIUI</a>
          <span>|</span>
          <a href>IoT</a>
          <span>|</span>
          <a href>云服务</a>
          <span>|</span>
          <a href>金融</a>
          <span>|</span>
          <a href>有品</a>
          <span>|</span>
          <a href>小爱开放平台</a>
          <span>|</span>
          <a href>政企服务</a>
          <span>|</span>
          <a href>Select Region</a>
        </div>
        <div class="topbar-cart logined" v-if="isLoggedIn">
          <a href>
            <i class="iconfont">&#xe60c;</i>购物车
            <span>（0）</span>
          </a>
        </div>
        <div class="topbar-info clearfix">
          <template v-if="!isLoggedIn">
            <router-link to="/login" class="login">登录</router-link>
            <span>|</span>
            <router-link to="/login" class="register">注册</router-link>
            <span>|</span>
          </template>
          <template v-else>
            <a class="logined" href="###">{{ user.name }}</a>
            <a href="###" class="sep">消息通知</a>
            <a href="###" class="sep logined logout" @click="handelLogout">登出</a>
          </template>
        </div>
      </div>
    </div>

    <div class="header">
      <div class="container">
        <div class="header-logo">
          <a href="#" class="lr">小米官网</a>
        </div>
        <div class="header-nav">
          <ul class="nav-list clearfix">
            <li class="nav-category">
              <a href>全部商品分类</a>
              <div class="category-list">
                <ul>
                  <li v-for="(left, index) in menuLeft" :key="`left_${index}`" class="tpl-cat">
                    <a class="title">
                      <span>{{ left.name }}</span>
                      <i class="iconfont"></i>
                    </a>
                    <div v-if="left.list" class="children clearfix">
                      <ul class="children-list">
                        <li
                          class="tpl-children"
                          v-for="(list, index) in left.list"
                          :key="`list_${index}`"
                        >
                          <a href>
                            <img :src="list.img" alt />
                            <span class="text">{{ list.name }}</span>
                          </a>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
              </div>
            </li>
            <li class="nav-item tpl-nav" v-for="(top, index) in menuTop" :key="`top_${index}`">
              <a href>{{ top.name }}</a>
            </li>
          </ul>
        </div>
        <div class="header-search">
          <form action class="search-form">
            <input type="search" name="keyword" class="search-text" />
            <button class="search-btn iconfont">&#xe616;</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../utis/api";
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      menuTop: [],
      menuLeft: []
    };
  },
  created() {
    // 通过？传参，注意这里回调函数需要用箭头函数
    api.get("/menu?type=top").then(data => {
      this.menuTop = data;
    });
    api.get("/menu?type=left").then(data => {
      this.menuLeft = data;
    });
  },
  computed: {
    ...mapState("user", ["isLoggedIn", "user"])
  },
  methods: {
    handelLogout() {
      this.logout();
    },
    ...mapActions("user", ["logout"])
  }
};
</script>

<style lang="scss" scoped>
@import url(../css/iconfont.css);
@import url(../css/style.css);
</style>